//ChatAppContext.js
import React,{useState,useEffect} from 'react';
import { useRouter } from 'next/router';


import { CheckIfWalletConnected,connectWallet,connectingWithContract } from '@/Utils/apiFeature';

export const ChatAppContext=React.createContext();


export const ChatAppProvider=({children})=>{
    const [account,setAccount]=useState("");
    const [userName,setUserName]=useState("");
    const [friendLists,setFriendLists]=useState([]);
    const [friendMsg,setFriendmsg]=useState([]);
    const [loading,setLoading]=useState(false);
    const [userLists,setUserLists]=useState([]);
    const [error,setError]=useState("");

    //User Data
    const [currentUserName, setCurrentUserName]=useState("")
    const [currentUserAddress, setCurrentUserAddress]=useState("")


    const router = useRouter();

    const fetchData = async()=>{
        try {
            // connection with contract
            const contract=await connectingWithContract();
            const connectAccount=await connectWallet();
            setAccount(connectAccount);

            const userName=await contract.getUsername(connectAccount);
            setUserName(userName);

            const frientLists=await contract.getMyFriendList();
            setFriendLists(frientLists);

            const userList=await contract.getAllUsers();
            setUserLists(userList);
            
        } catch (error) {
            setError("Please Install and connect your wallet")
        }
    };
    useEffect(()=>{
        fetchData();
    },[]);

    const readMessage= async(friendAddress)=>{
        try {
            const contract= await connectingWithContract();
            const read = await contract.readMessage(friendAddress);
            setFriendmsg(read);
        } catch (error) {
            setError("Currently No messages")
        }
    }

    const createAccount =async(name, accountAddress)=>{
        try {
            if(!name || !accountAddress) return setError("Account Address and Name has be provided");
            const contract= await  connectingWithContract();
            const getCreatedUser= await contract.createAccount(name);
            setLoading(true);
            await getCreatedUser.wait();
            setLoading(false);
            window.location.reload();
        } catch (error) {
            setError("error while creating the account.Please Relaod the Browser.")
            
        }
    }

    const addFriend = async(accountAddress,name)=>{
        try {
            if(!name || !accountAddress) return setError("Please Provide the name and address ")
            const contract= await  connectingWithContract();
            const addMyFriend= await contract.addFriend(accountAddress,name);
            setLoading(true);
            await addMyFriend.wait();
            setLoading(false);
            router.push("/");
            window.location.reload();
        } catch (error) {
            setError("Error while adding your friend. Try Adding again!!")
        }
    }

    const sendMessage=async({msg, address})=>{
        try {
            if(!msg || !address) return  setError("Please Type your message")
            const contract= await  connectingWithContract();
            const addMyFriend= await contract.sendMessage(accountAddress,msg);
            setLoading(true);
            await addMyFriend.wait();
            setLoading(false);
            window.location.reload();
        } catch (error) {
            setError("Please Reload and try again.") 
        }
    }

    const readUser=async(userAddress)=>{
        const contract= await  connectingWithContract();
        const userName=contract.getUsername(userAddress);
        setCurrentUserName(userName);
        setCurrentUserAddress(userAddress);

    }

    return(
        <ChatAppContext.Provider value={{readMessage,createAccount,addFriend,sendMessage,readUser,
            account,
            userName,
            friendLists,
            friendMsg,
            loading,
            userLists,
            error,
            currentUserName,
            currentUserAddress,
            setFriendmsg,
            CheckIfWalletConnected,
            connectWallet,
        }}>
            {children}
        </ChatAppContext.Provider>
    );
};